.list {
  display: grid;
  max-height: min-content;
  max-width: calc(100vw - 48px);
  grid-template-columns: repeat(auto-fill, minmax(154px, 1fr));
  grid-gap: 16px;
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style: none;
  margin-left: auto;
  margin-right: auto;
  padding-top: 16px;
  padding-bottom: 40px;
}

.photo {
  display: block;
  width: 100%;
  object-fit: contain;
}

.notFount{
  padding-bottom: 60px;
  font-size: 24px;
  font-weight: 500;
}

.name{
  margin-bottom: 10px;
}
.character{
  font-weight: 500;
}
